@import "../../assets/css/variables.scss";
@import "../../assets/css/mixins.scss";
.m-editor {
  display: grid;
  width: 24rem;
  border-radius: $border-radius-popup;
  grid-template-rows: repeat(7, 4rem);
  grid-template-columns: 8rem 16rem;
  background-color: $bg-board;
  overflow: hidden;
  align-items: center;
  & > label,
  &__clear-button {
    color: $color-font-passive;
    padding-left: 1rem;
  }
  & > .m-counter,
  &__date {
    padding-right: 1rem;
  }
  &__date {
    position: relative;
    padding-right: 1rem;
    text-align: center;
    line-height: $height-form-line;
    color: $color-font-active !important;
    &-text {
      border-bottom: 2px solid rgba(128, 128, 128, 0.295);
      background: no-repeat right border-box
        url("../../assets/image/calendar.svg");
      background-size: 1rem;
    }
  }
  &__clear-button {
    @include clear-style-button;
    text-align: start;
    font-size: 1.2rem;
    width: 100%;
    background-color: transparent;
    transition: all 0.2s ease;
    text-decoration: underline;
    cursor: pointer;
    &:hover:not([disabled]) {
      transform: scale(1.1);
    }
    &:disabled {
      color: rgb(226, 226, 226);
      cursor: default;
    }
  }
  &__control {
    display: flex;
    column-gap: 0.15rem;
    justify-content: stretch;
    height: 100%;
    width: 100%;
    grid-column: 1/3;
    padding: 0 -1rem;
    background-color: rgb(209, 207, 207);
    border-bottom-left-radius: $border-radius-popup;
    border-bottom-right-radius: $border-radius-popup;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.295);
    overflow: hidden;
    & > button {
      @include clear-style-button;
      text-align: start;
      background-color: $bg-board;
      display: flex;
      justify-content: center;
      align-items: center;
      flex: 1 1 auto;
      font-size: 1rem;
      &:hover:not([disabled]) {
        background-color: $bg-hover;
      }
      & img {
        height: 1rem;
        width: 1rem;
        margin-right: 0.3rem;
      }
      &.m-cancel {
        color: #ce1010;
      }
      &.m-save {
        color: #456945;
        &:disabled {
          color: rgb(226, 226, 226);
        }
      }
    }
  }
}
