@import "../../assets/css/variables.scss";
@import "../../assets/css/mixins.scss";
.m-counter {
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    column-gap: 0.5rem;
    border-bottom: 2px solid rgba(128, 128, 128, 0.295);
    height: $height-form-line;
    max-width: 20rem;
  }
  &__input {
    @include clear-style-button;
    text-align: center;
    font-size: 1.2rem;
    width: 100%;
    background-color: transparent;
    transition: all 0.2s ease;
  }
  &__incr,
  &__decr {
    cursor: pointer;
    &:hover {
      transform: scale(1.2);
    }
  }
}

// ! Delete arrow in input
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}
