@import "../../assets/css/mixins.scss";
.m-month {
  display: flex;
  column-gap: 0.5rem;
  align-items: center;
  font: {
    weight: 500;
    size: 1.5rem;
  }
  &__next {
    font: inherit;
    cursor: pointer;
    @include clear-style-button;
    margin: 0;
    padding: 0;
    background: transparent;
    &:hover {
      font-weight: 800;
      color: red;
    }
  }
  &__name {
    width: 12rem;
    text-align: center;
  }
}
