@import "../../assets/css/variables.scss";

$indent: 0.6rem;
.m-day {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  position: relative;
  border: 1px solid $border-color-board;
  &__body {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    & img {
      width: 2rem;
      height: 2rem;
      transition: all 0.3s ease;
      &:hover {
        transform: scale(1.2);
      }
    }
  }
  &.today {
    background-color: $bg-today;
  }
  &.success {
    border-top: $border-top-success;
  }
  &.warning {
    border-top: $border-top-warning;
  }
  &__header-update {
    color: transparent;
    transition: all 0.3s ease;
    cursor: pointer;
  }
  &__popup {
    position: absolute;
    line-height: 1.8rem;
    width: 17rem;
    top: 2rem;
    right: -14rem;
    padding: 1rem 1rem;
    background-color: white;
    border-radius: $border-radius-popup;
    transform: scale(0);
    transition: all 0.3s ease;
    transition-delay: 0.5s;
  }
  &:hover {
    background-color: $bg-hover;
    .m-day__header-update {
      color: black;
    }
    .m-day__popup {
      z-index: 10;
      box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
      transform: scale(1);
    }
  }
  @each $day, $color in $border-weekday {
    &.#{$day} {
      border-right: 2px solid $color;
      @if $day != sun {
        border-left: none;
      }
    }
  }
  &__header-date,
  &__header-update {
    position: absolute;
    top: $indent;
  }
  &__header-date {
    left: $indent;
  }
  &__header-update {
    right: $indent;
  }
}
.another {
  color: $color-font-passive;
}
