@import "../../assets/css/variables.scss";
.m-board {
  position: relative;
  display: grid;
  grid-template-rows: 2rem repeat(5, 1fr);
  grid-template-columns: repeat(7, minmax(6rem, 1fr));
  max-width: 1200px;
  height: calc(100vh - 2rem - #{$height-header});
  border-radius: 1rem;
  background-color: white;
  transition: 1s;
  &__weekdays {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &.large {
    grid-template-rows: 2rem repeat(6, 1fr);
  }
}
