@import "../../assets/css/variables.scss";
$size-icon: 1.5rem;
.m-help {
  background-color: $bg-board;
  max-width: 20rem;
  margin: 1rem;
  &__title {
    font: {
      size: 1.7rem;
      weight: 800;
    }
    padding-bottom: 3.5rem;
  }
  &__section-title {
    font: {
      size: 1.5rem;
      weight: 400;
    }
    padding-bottom: 0.8rem;
  }
  &__section-images {
    display: flex;
    align-items: center;
    font: {
      size: 1rem;
      weight: 500;
    }
    padding-bottom: 0.8rem;
  }
  &__section-text {
    color: $color-font-passive;
    font: {
      size: 1rem;
      weight: 400;
    }
    padding-bottom: 2rem;
  }
  & * > img {
    height: $size-icon;
    width: $size-icon;
    &.large {
      height: $size-icon * 1.4;
      width: $size-icon * 1.4;
    }
    &.small {
      height: $size-icon * 0.6;
      width: $size-icon * 0.6;
    }
  }
}
