@import "../../assets/css/variables.scss";

.m-status {
  display: flex;
  column-gap: 2rem;
  align-items: center;
  & .m-status__line {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    height: $height-form-line;
    width: $height-form-line;
  }
  & .m-status__line.active {
    border: 1px solid green;
    border-radius: 50%;
  }
}
