@import "../../assets/css/variables.scss";
$colorborder: #d2d8da;
.m-buttonhdr {
  display: flex;
  height: $height-header;
  width: 4.5rem;
  border-radius: #{$height-header / 2};
  border: 1px solid $colorborder;
  align-items: center;
  justify-content: center;
  &.depressed {
    box-shadow: inset 0.1rem 0.1rem 0.2rem $colorborder,
      0.1rem 0.1rem 0.2rem white;
  }
  & label.star,
  & img {
    font-size: 1.7rem;
    transition: all 0.3s ease;
    &:hover {
      transform: scale(1.2);
    }
  }
  & img {
    cursor: grab;
    width: #{$height-header * 0.7};
    height: #{$height-header * 0.7};
  }
}
