$border-radius-popup: 1rem;
$height-header: 3rem;
$height-form-line: 2.5rem;
// ? FC Board
$border-color-board: rgb(206, 205, 205);
$border-top-success: 2px solid green;
$border-top-warning: 2px solid rgb(226, 16, 16);
$border-weekday: (
  sun: rgb(235, 63, 63),
  mon: rgb(238, 144, 56),
  tue: rgb(226, 238, 56),
  wed: rgb(92, 238, 56),
  thu: rgb(56, 129, 238),
  fri: rgb(208, 56, 238),
);
$bg-board: white;
$bg-today: rgb(205, 228, 230);
$bg-hover: rgb(231, 230, 228);

// ? Fonts color
$color-font-active: black;
$color-font-passive: rgb(148, 145, 145);
